*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    min-height: 100vh; /* cuerpo ocupa toda la altura */
    background-image: url("/img/fondo.png");
    background-size: cover;        /* ocupa toda la pantalla */
    background-repeat: no-repeat;  /* no se repite */
    background-position: center;   /* centrado */
    background-attachment: fixed;  /* el fondo queda fijo */
    font-size: 18px;
}

/* CONTENEDOR GLOBAL PARA TODAS LAS PÁGINAS */
main {
    max-width: 1200px;
    margin: 0 auto;
    padding: 80px 20px 40px;
}

/* HACER QUE TODAS TUS TARJETAS SEAN MÁS GRANDES */
.section-card {
    background: rgba(0,0,0,0.45);
    padding: 25px 30px;
    border-radius: 18px;
    backdrop-filter: blur(12px);
    box-shadow: 0 0 20px rgba(0,0,0,0.4);
    color: #f2f2f2;
    font-size: 1.1rem;
}

/* TEXTO MÁS GRANDE */
.section-card h2,
.section-card h3,
.section-card h4 {
    font-size: 1.4rem;
}

.section-card p {
    font-size: 1.05rem;
    line-height: 1.6;
}

/* GRID PARA DISTRIBUIR TRES TARJETAS */
.page-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 35px;
    margin-top: 20px;
}
